import axios from "axios";
import React, { useEffect, useState } from "react";
import Percent from "./Percent";

const Header = () => {
  const [dataHeader, setDataHeader] = useState([]);

  useEffect(() => {
    axios
      .get("https://api.coingecko.com/api/v3/global")
      .then((res) => setDataHeader(res.data.data));
  }, []);

  return (
    <div className="header-container">
      <div className="market-info">
        <h2>Informations March</h2>
        {dataHeader && (
          <li>Crypto-monnaies : {dataHeader.active_cryptocurrencies}</li>
        )}
        {dataHeader && <li>Marchs : {dataHeader.markets}</li>}
        <li>
          Global Market Cap :{" "}
          <Percent percent={dataHeader.market_cap_change_percentage_24h_usd} />
        </li>

        <li>
          BTC Dominance :{" "}
          {dataHeader.market_cap_percentage &&
            dataHeader.market_cap_percentage.btc.toFixed(1)}
          %
        </li>

        <li>
          ETH Dominance :{" "}
          {dataHeader.market_cap_percentage &&
            dataHeader.market_cap_percentage.eth.toFixed(1)}
          %
        </li>
      </div>
    </div>
  );
};

export default Header;
