import React, { useEffect, useState } from "react";
import Tokens from "./Tokens";
import Totop from "./Totop";
import { useSelector } from "react-redux";
import { isStableCoin } from "./Utils";

const Main = ({ dataCoins }) => {
  const [topValue, setTopValue] = useState(50);
  const [orderBy, setOrderBy] = useState("");
  const showStable = useSelector((state) => state.stableReducer.showStable);
  const showFavList = useSelector((state) => state.listReducer.showList);

  const headerTitle = [
    "Prix",
    "MarketCap",
    "Volume",
    "1h",
    "1j",
    "1s",
    "1m",
    "6m",
    "1a",
    "ATH",
  ];

  return (
    <div className="main-container">
      <div className="header">
        <div className="input-container">
          <span>
            <label htmlFor="number">Top </label>
            <input
              type="number"
              id="number"
              value={topValue}
              onChange={(e) => setTopValue(e.target.value)}
            />
          </span>
          <div className="range-container">
            <input
              type="range"
              id="range"
              value={topValue}
              min={1}
              max={250}
              onChange={(e) => setTopValue(e.target.value)}
            />
          </div>
          <Totop />
        </div>
        <div className="headerTitle">
          {headerTitle.map((title) => {
            return (
              <li key={title}>
                <input
                  type="radio"
                  name="header-element"
                  id={title}
                  defaultChecked={
                    title === orderBy || title === orderBy + "reverse"
                      ? true
                      : false
                  }
                  onClick={() => {
                    setOrderBy(title);
                    if (orderBy === title) {
                      setOrderBy(title + "reverse");
                    } else {
                      setOrderBy(title);
                    }
                  }}
                />
                <label htmlFor={title}>{title}</label>
              </li>
            );
          })}
        </div>
      </div>
      {dataCoins &&
        dataCoins
          .slice(0, topValue)
          .filter((coin) => {
            if (showStable) {
              return coin;
            } else {
              if (isStableCoin(coin.symbol)) {
                return coin;
              }
            }
          })
          .filter((coin) => {
            if (showFavList) {
              let list = window.localStorage.tokenList.split(",");
              if (list.includes(coin.id)) {
                return coin;
              }
            } else {
              return coin;
            }
          })
          .sort((a, b) => {
            switch (orderBy) {
              case "Prix":
                return b.current_price - a.current_price;
              case "Prixreverse":
                return a.current_price - b.current_price;
              case "MarketCap":
                return b.market_cap - a.market_cap;
              case "MarketCapreverse":
                return a.market_cap - b.market_cap;
              case "Volume":
                return b.total_volume - a.total_volume;
              case "Volumereverse":
                return a.total_volume - b.total_volume;
              case "1h":
                return (
                  b.price_change_percentage_1h_in_currency -
                  a.price_change_percentage_1h_in_currency
                );
              case "1hreverse":
                return (
                  a.price_change_percentage_1h_in_currency -
                  b.price_change_percentage_1h_in_currency
                );
              case "1j":
                return b.price_change_24h - a.price_change_24h;
              case "1jreverse":
                return a.price_change_24h - b.price_change_24h;
              case "1s":
                return (
                  b.price_change_percentage_7d_in_currency -
                  a.price_change_percentage_7d_in_currency
                );
              case "1sreverse":
                return (
                  a.price_change_percentage_7d_in_currency -
                  b.price_change_percentage_7d_in_currency
                );
              case "1m":
                return (
                  b.price_change_percentage_30d_in_currency -
                  a.price_change_percentage_30d_in_currency
                );
              case "1mreverse":
                return (
                  a.price_change_percentage_30d_in_currency -
                  b.price_change_percentage_30d_in_currency
                );
              case "6m":
                return (
                  b.price_change_percentage_200d_in_currency -
                  a.price_change_percentage_200d_in_currency
                );
              case "6mreverse":
                return (
                  a.price_change_percentage_200d_in_currency -
                  b.price_change_percentage_200d_in_currency
                );
              case "1a":
                return (
                  b.price_change_percentage_1y_in_currency -
                  a.price_change_percentage_1y_in_currency
                );
              case "1areverse":
                return (
                  a.price_change_percentage_1y_in_currency -
                  b.price_change_percentage_1y_in_currency
                );
              case "ATH":
                return b.ath_change_percentage - a.ath_change_percentage;
              case "ATHreverse":
                return a.ath_change_percentage - b.ath_change_percentage;
            }
          })

          .map((token, index) => (
            <Tokens index={index} token={token} key={index} />
          ))}
    </div>
  );
};

export default Main;
