import { toHaveAttribute } from "@testing-library/jest-dom/matchers";
import React, { useEffect, useState } from "react";

const Staricon = ({ tokenId }) => {
  const [isLiked, setIsLiked] = useState(false);
  useEffect(() => {
    if (window.localStorage.tokenList) {
      let favList = window.localStorage.tokenList.split(",");
      if (favList.includes(tokenId)) {
        setIsLiked(true);
      } else {
        setIsLiked(false);
      }
    }
  }, [tokenId]);

  const idChecker = (id) => {
    let favList = null;

    if (window.localStorage.tokenList) {
      favList = window.localStorage.tokenList.split(",");
    }

    if (favList) {
      if (favList.includes(id)) {
        window.localStorage.tokenList = favList.filter((coin) => coin !== id);
        setIsLiked(false);
      } else {
        window.localStorage.tokenList = [...favList, tokenId];
        setIsLiked(true);
      }
    } else {
      window.localStorage.tokenList = tokenId;
      setIsLiked(true);
    }
  };

  return (
    <div className="staricon-container" onClick={() => idChecker(tokenId)}>
      {isLiked ? (
        <i className="fa-solid fa-star"></i>
      ) : (
        <i className="fa-regular fa-star"></i>
      )}
    </div>
  );
};

export default Staricon;
