import React, { useState } from "react";
import Percent from "./Percent";
import Staricon from "./Staricon";
const Tokens = ({ token, index }) => {
  const formatPrice = (price) => {
    if (Math.round(price).toString().length < 4) {
      return new Intl.NumberFormat("us-US", {
        minimumFractionDigits: 2,
        maximumFractionDigits: 7,
      }).format(price);
    } else {
      return price;
    }
  };
  const formatPrice2 = (price) => {
    if (Math.round(price).toString().length > 6) {
      return new Intl.NumberFormat("fr-FR", {
        notation: "compact",
        compactDisplay: "short",
      }).format(price);
    } else {
      return price;
    }
  };

  const mktCapFormateur = (num) => {
    let newNum = String(num).split("").slice(0, -6);
    return Number(newNum.join(""));
  };

  return (
    <div className="tokens-container">
      <div className="left-part">
        <Staricon tokenId={token.id} />
        <p>{index + 1}</p>
        <div className="img">
          <img src={token.image} alt="image-token" />
        </div>
        <div className="info">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            fill="none"
            viewBox="0 0 24 24"
            strokeWidth="1.5"
            stroke="currentColor"
            className="w-6 h-6"
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              d="M3 13.125C3 12.504 3.504 12 4.125 12h2.25c.621 0 1.125.504 1.125 1.125v6.75C7.5 20.496 6.996 21 6.375 21h-2.25A1.125 1.125 0 013 19.875v-6.75zM9.75 8.625c0-.621.504-1.125 1.125-1.125h2.25c.621 0 1.125.504 1.125 1.125v11.25c0 .621-.504 1.125-1.125 1.125h-2.25a1.125 1.125 0 01-1.125-1.125V8.625zM16.5 4.125c0-.621.504-1.125 1.125-1.125h2.25C20.496 3 21 3.504 21 4.125v15.75c0 .621-.504 1.125-1.125 1.125h-2.25a1.125 1.125 0 01-1.125-1.125V4.125z"
            />
          </svg>

          <h2>
            {token.id.charAt(0).toUpperCase() + token.id.slice(1)}
            <span> /{token.symbol.toUpperCase()}</span>
          </h2>
          <a
            href={
              "https://www.coingecko.com/en/coins/" + token.id.toLowerCase()
            }
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              fill="none"
              viewBox="0 0 24 24"
              strokeWidth="0.5"
              stroke="currentColor"
              className="w-6 h-6"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                d="M11.25 11.25l.041-.02a.75.75 0 011.063.852l-.708 2.836a.75.75 0 001.063.853l.041-.021M21 12a9 9 0 11-18 0 9 9 0 0118 0zm-9-3.75h.008v.008H12V8.25z"
              />
            </svg>
          </a>
        </div>
      </div>
      <div className="right-part">
        <p>{formatPrice(token.current_price)} $</p>
        <p>{mktCapFormateur(token.market_cap).toLocaleString()} M$</p>
        <p>{formatPrice2(token.total_volume).toLocaleString()} $</p>
        <Percent percent={token.price_change_percentage_1h_in_currency} />
        <Percent percent={token.price_change_24h} />
        <Percent percent={token.price_change_percentage_7d_in_currency} />
        <Percent percent={token.price_change_percentage_30d_in_currency} />
        <Percent percent={token.price_change_percentage_200d_in_currency} />
        <Percent percent={token.price_change_percentage_1y_in_currency} />
        <Percent percent={token.ath_change_percentage} />
      </div>
    </div>
  );
};

export default Tokens;
