import React, { useEffect, useState } from "react";

const Percent = ({ percent }) => {
  const [color, setColor] = useState("");

  useEffect(() => {
    if (percent < 0) {
      setColor("red");
    } else if ((percent = 0)) {
      setColor("white");
    } else {
      setColor("green");
    }
  });

  return (
    <div className="percent-container">
      <strong>
        {" "}
        <p style={{ color: color }}>{percent ? percent.toFixed(1) : "-"} %</p>
      </strong>
    </div>
  );
};

export default Percent;
