import React, { useEffect, useState } from "react";
import { Tooltip, Treemap } from "recharts";

const Chart = ({ dataCoins }) => {
  const [dataCoin, setDataCoin] = useState([]);

  useEffect(() => {
    const chartData = [];
    if (dataCoins.length > 0)
      for (let i = 0; i < 45; i++) {
        chartData.push({
          name:
            dataCoins[i].symbol +
            " " +
            dataCoins[i].market_cap_change_percentage_24h.toFixed(1),
          size: dataCoins[i].market_cap,
          fill: colorPickers(dataCoins[i].market_cap_change_percentage_24h),
        });
      }
    setDataCoin(chartData);
  }, [dataCoins]);

  function CustomTooltip({ payload, active }) {
    if (active) {
      return (
        <div className="custom-tooltip">
          <p className="label">{`${payload[0].payload.name}`}</p>
        </div>
      );
    }

    return null;
  }

  const colorPickers = (number) => {
    if (number >= 20) {
      return "rgb(0, 253, 148)";
    } else if (number >= 5) {
      return "rgb(2, 172, 81)";
    } else if (number >= 0) {
      return "rgb(255, 111, 86)";
    } else if (number >= -5) {
      return "rgb(251, 69, 63)";
    } else if (number >= -20) {
      return "black";
    }
  };

  return (
    <div className="chart-container">
      <div className="treemap">
        <Treemap
          width={500}
          height={181}
          data={dataCoin}
          dataKey="size"
          stroke="rgb(51,51,51)"
          fill="black"
          aspectRatio="1"
        >
          <Tooltip content={CustomTooltip} />
        </Treemap>
      </div>
    </div>
  );
};

export default Chart;
